<template>
  <div id="app" class="container">
    <div class="row">
      <div class="col-md-6 offset-md-3 py-5">
        <h1>Generate a thumbnail of a website</h1>
        <form v-on:submit.prevent="makeWebsiteThumbnail">
          <div class="form-group">
            <input v-model="websiteUrl" placeholder="Enter a website" type="text" id="website-input" class="form-control">
          </div>
          <div class="form-group">
            <button class="btn btn-primary">Generate!</button>
          </div>
          <img :src="thumbnailUrl" />
        </form>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios';
  export default {
    name : 'App',
    data() { return {
      websiteUrl : '',
      thumbnailUrl: '',
    } },
    methods : {
      makeWebsiteThumbnail() {
        axios.post("https://shot.screenshotapi.net/screenshot?token=QX33FDK-34944RG-PVSFV74-2PVDP0X&url=https%3A%2F%2Fgoogle.com&output=image&file_type=png&wait_for_event=load", {
          token : "QX33FDK-34944RG-PVSFV74-2PVDP0X",
          url : this.websiteUrl,
          width : 1920,
          height : 1080,
          output : 'json',
          thumbnail_width : 300
      })
      .then((response) => {
        this.thumbnailUrl = response.data.screenshot;
      })
      .catch((error) => {
        window.alert(`The API returned an error: ${error}`);
      })
      }
    }
  }
</script>